.main{min-height:100vh}.hero{background:linear-gradient(135deg, #1a1a1a 0%, #333 100%);color:#fff;padding:6rem 0;text-align:center}.hero .heroTitle{font-size:3rem;margin-bottom:1rem}.hero .heroText{font-size:1.2rem;margin-bottom:2rem;opacity:.9}.container{max-width:1200px;margin:0 auto;padding:0 1rem}.button{background-color:#0070f3;color:#fff;border:none;padding:1rem 2rem;font-size:1.1rem;border-radius:5px;cursor:pointer;transition:background-color .3s ease}.button:hover{background-color:#0051a2}.features{padding:4rem 0;background-color:#f5f5f5}.features .featuresTitle{text-align:center;font-size:2rem;margin-bottom:3rem}.grid{display:grid;grid-template-columns:repeat(auto-fit, minmax(250px, 1fr));gap:2rem}.card{background:#fff;padding:2rem;border-radius:10px;box-shadow:0 2px 4px rgba(0,0,0,.1);text-align:center}.card .cardTitle{font-size:1.5rem;margin-bottom:1rem;color:#1a1a1a}.card .cardText{color:#666;line-height:1.6}